import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import PrivateRoute from './src/pages/PrivateRoute'

/** css */
import './App.css';
import "./assets/css/login.css";
import "./assets/css/layout.css";
import "./assets/css/body.css";

/** Rutas */
import Login from './src/pages/Login'
import Home from './src/pages/Home'
import Layout from './src/pages/Layout';
import CompanyList from './src/pages/company';
import CompanyRegistry from './src/pages/company/Registry';
import Credits from './src/pages/credits';

const NotFoud = () => (
  <div>Error 404. <br /> Pagina no encontrada</div>
)

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path='/' component={Login} />
        <Route exact path='/login' component={Login} />
        <Layout>
          {/** Home */}
          <PrivateRoute exact path='/home'>
            <Home />
          </PrivateRoute>

          {/** Company */}
          <PrivateRoute exact path='/empresas/'>
            <CompanyList />
          </PrivateRoute>
          <PrivateRoute exact path='/empresas/registro'>
            <CompanyRegistry />
          </PrivateRoute>

        {/** Credits */}
          <PrivateRoute exact path='/creditos/'>
            <Credits />
          </PrivateRoute>
        </Layout>


        {/** 404 - Not Found */}
        <Route exact path='*' component={NotFoud} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
