import React from 'react'
import { reduxForm, Field } from 'redux-form'
import Logo from '../../assets/images/logo.svg'

const MyField = ({ input, meta, type, placeholder, name }) => (
    <React.Fragment>
        <input {...input} type={type} className="input-login" name={name} placeholder={placeholder} required />
        {
            meta.touched && meta.error && <span>{meta.error}</span>
        }
    </React.Fragment>
)


const LoginV = ({ handleSubmit, submitting, message }) => (
     <main className="login-fondo">
        <div className="login-container">
            <img src={Logo} alt="logo" className="logo" />
            <form onSubmit={handleSubmit} className="form-login" encType="multipart/form-data">
                <span className="title-login">Welcome to Libra!</span>
                <Field 
                    name="u_ideNum" 
                    component={MyField} 
                    type="text" 
                    placeholder="Nombre"
                />
                <Field 
                    name="u_pass" 
                    component={MyField} 
                    type="password" 
                    placeholder="Contraseña"
                />
                <div className="error-message" style={message && {opacity: 1, height: 50}}>
                    <span>{message}</span>
                </div>

                <button type="button" className="link">¿Olvidaste tu contraseña?</button>

                <button 
                    type="submit" 
                    className={`btn-submit ${submitting && "bg-gray"}`}
                    disabled={submitting}
                >Enviar</button>
            </form>
        </div>
    </main>
)

const LoginVForm = reduxForm({
    form: 'loginForm',
})(LoginV)

export default LoginVForm;