/** Apartado de los usuarios */
export const apiGetLogin = (url, values) => () => (
    fetch(`${url}user/login`, {
        method: 'POST',
        headers: {
            Accept: 'application/json',
                    'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            u_phoEma: values.u_ideNum,
            u_pass: values.u_pass
        }),
    }).then(v => v.json() )
    .then(r => {

        if (r.success === 0) {
            return r.message;
        } else {
            localStorage.setItem('userActive', JSON.stringify(r))
            return r.message;
        }
    }).catch(e => console.log(e))
)