import React from 'react'
import { IconMessage, IconNotification, IconUser } from '../../assets/icons'

const Header = () => (
    <div className="header">
        <div className="icon-header-container">
            <IconNotification width={25} height={25} color="#ffffff" />
        </div>
        <div className="icon-header-container">
            <IconMessage width={25} height={25} color="#ffffff" />
        </div>
        <div className="icon-header-container">
            <IconUser width={25} height={25} color="#ffffff" />
            <span className="user-name">Niulwinrios87</span>
        </div>
    </div>
)
export default Header