import React, { Component } from 'react'

import Header from "../components/Header"
import Logo from "../components/Logo"
import Aside from "../components/Aside"
import { withRouter } from 'react-router-dom'


class Layout extends Component {
    constructor(props){
        super(props)
        this.submenuRef = React.createRef()
    }
    handleSession = () => {
        localStorage.removeItem("userActive")
        this.props.history.push('/')
    }
    handleMenu = e => {
        const x = document.getElementById(e.currentTarget.id)

        if (x.querySelector(".menu-oculto") != null )
            x.querySelector(".menu-oculto").classList.toggle("active-menu")
        
    }
    render() {
        return (
        <div className="main">
            <Logo />
            <Header />
            <Aside handleSession={this.handleSession} handleMenu={this.handleMenu} submenuRef={this.submenuRef} />
            {this.props.children}
        </div>
        )
    }
}

export default withRouter(Layout)