import React from 'react'
import { IconEnterprise } from '../../../assets/icons'

const BannerV = ({ title, search = false }) => (
    <div className="banner-title-container">
        <div className="flex">
            <IconEnterprise width={30} height={30} color="#1d6fb8" />
            <h4 className="banner-title">{title}</h4>
        </div>
        {search &&
            <div>
                <span className="banner-span">Categoría</span>
                <select className="banner-select">
                    <option>Educación</option> 
                    <option>Elemento 1</option> 
                </select>
            </div>
        }
    </div>
)


export default BannerV