import React from 'react'
import BannerV from '../common/BannerV';
import { IconSearch } from '../../../assets/icons';

const index = () => (
    <div className="body">
        <BannerV title="CRÉDITOS" search={true} />

        <div className="table-list">
            <div className="flex space-between">
                <h4 className="banner-title margin-sprm">CRÉDITOS EMPLEADOS</h4>
                <div className="flex">
                    <span className="banner-span">Buscar por número de documento</span>
                    <div className="relative">
                        <input type="text" className="input-search banner-select input-search" />
                        <button type="button" className="btn btn-search"><IconSearch width={15} height={15} color="#ffffff" /></button>
                    </div>
                </div>
            </div>

            {/** Table */}
            <div className="table-contain">
                <table>
                    <tr>
                        <th>CEDULA</th>
                        <th>NOMBRE</th>
                        <th>EMPRESA</th>
                        <th>VALOR DEL CREDITO</th>
                        <th>CUOTAS</th>
                        <th>CAPITAL</th>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <td>0123456789</td>
                        <td>Niulwin Jose Rios Diaz</td>
                        <td>CAE GROUP S.A.S.</td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </table>
            </div>
        </div>

    </div>
)

export default index;