import React from 'react'
import BannerV from '../common/BannerV'
import InputText from '../common/InputText'



const RegistryV = () => (
    <div className="body">
        <BannerV title="REGISTRO DE EMPRESA" />
        <h5 className="banner-title banner-subtitle">Epresas del sector privado</h5>
        <div className="container-input-row border-bt-blue">
            <InputText label="Razón Social"/>
            <InputText label="Nit"/>
            <InputText label="Dirección"/>
            <InputText label="Teléfono"/>
        </div>
        <h5 className="banner-title banner-subtitle">Datos del tesorero de la empresa</h5>
        <div className="container-input-row">
            <InputText label="Nombre del Tesorero"/>
            <InputText label="Contacto del tesorero"/>
        </div>
        <div>
            <button className="btn btn-add">Agregar Empresa</button>
        </div>
    </div>
)

export default RegistryV