import React from 'react'
import { Scrollbars } from 'react-custom-scrollbars';
import { Link } from 'react-router-dom';
import { IconEnterprise, IconHome, IconUserInvestor } from '../../assets/icons';

const Aside = ({ handleSession, handleMenu, submenuRef }) => (
    <div className="aside">
        <Scrollbars style={{flex: .8}}
            autoHide
            autoHideTimeout={1000}
            autoHideDuration={200}
            universal={true}
        >
            <ul className="list-nav">
                <li className="list-element-nav">
                    <Link to="/home" className="list-item" id="home" onClick={handleMenu}>
                        <IconHome width={25} height={25} color="#29ABE2" />
                        <span className="span-text">HOME</span>
                    </Link>
                </li>
                <li className="list-element-nav" id="button1" onClick={handleMenu} >
                    <button type="button" className="list-item btn btn-menu-show">
                        <IconEnterprise width={25} height={25} color="#29ABE2" />
                        <span className="span-text">EMPRESAS</span>
                    </button>
                    <div className="menu-oculto" ref={submenuRef}>
                        <Link className="link-nav" to="/empresas/">Lista de Empresas</Link>
                        <Link className="link-nav" to="/empresas/registro">Registro de Empresa</Link>
                        <Link className="link-nav" to="/registro">Registro de Nómina</Link>
                        <Link className="link-nav" to="/registro">Nómina</Link>
                        <Link className="link-nav" to="/registro">Créditos</Link>
                    </div>
                </li>
                <li className="list-element-nav" id="button2" onClick={handleMenu}>
                    <button type="button" to="/empresas" className="list-item btn btn-menu-show">
                        <IconUserInvestor width={25} height={25} color="#29ABE2" />
                        <span className="span-text">INVERSIONISTA</span>
                    </button>
                    <div className="menu-oculto" id="submenu">
                        <Link className="link-nav" to="/registro">Lista de Empresas</Link>
                        <Link className="link-nav" to="/registro">Registro de Empresa</Link>
                        <Link className="link-nav" to="/registro">Registro de Nómina</Link>
                        <Link className="link-nav" to="/registro">Nómina</Link>
                        <Link className="link-nav" to="/registro">Créditos</Link>
                    </div>
                </li>
                <li className="list-element-nav">
                     <Link to="/creditos" className="list-item" id="creditos" onClick={handleMenu}>
                        <IconUserInvestor width={25} height={25} color="#29ABE2" />
                        <span className="span-text">CREDITOS</span>
                    </Link>
                </li>
                <li className="list-element-nav">
                <div className="list-item">
                        <IconUserInvestor width={25} height={25} color="#29ABE2" />
                        <span className="span-text">SOLICITUDES</span>
                    </div>
                </li>
            </ul>
        </Scrollbars>
        <div className="logout-container">
            <button type="button" onClick={handleSession} className="link-nav text-right">Cerrar Sesión</button>
            <button type="button" className="link-nav text-right">Politica de privacidad</button>
        </div>
    </div>
)
export default Aside